import React from "react"
import loadable from '@loadable/component'
import * as homeStyles from "./home.module.css"
import { StaticImage } from "gatsby-plugin-image"


export default class Home extends React.Component {
    constructor(props) {
        super(props);
    }
    render(){
        return (
            <div className="mainBody">
                <div className={homeStyles.innerBox}>
                    <div className={homeStyles.box_contact}>
                        <h1>Hello World!</h1>
                        <p>
                            My name is Luis Inga, an IT and Computer systems engineer (Master’s degree) with more than eight years’ professional experience in functional analysis and software development in Linux and Windows enterprise environment. Solid knowledge of programming languages such as Java, C++, JavaScript, C, Python as well as experience in customer support area.
                        </p>
                        <p>
                            Creative and critical spirit, proactive, good team player, enjoys new challenges and expanding horizons. 
                        </p>
                    </div>
                    <div className={homeStyles.box_contact}>
                        <h2>Technologies used in portfolio</h2>
                        <div>			
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/javascript-icon.png" alt="Javascript Icon" layout="constrained"/>
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/css3-icon.png" alt="CSS3 Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/html5-icon.png" alt="Html5 Icon" layout="constrained"/>
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/angularjs-icon.png" alt="AngularJs Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/d3js-icon.png" alt="D3js Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/nodejs-icon.png" alt="NodeJs Icon" layout="constrained"/>                                                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/java-icon.png" alt="java icon" width={48} layout="fixed"/>
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/jersey-icon.png" alt="Jersey Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/spring-icon.png" alt="Spring Icon" layout="constrained"/>   
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/tomcat-icon.png" alt="Tomcat Icon" layout="constrained"/>     
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/apache-icon.png" alt="Apache Icon" layout="constrained"/>                                
                            </div>
                            
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/mysql-icon.png" alt="MySql Icon" layout="constrained"/>
                            </div>
                            
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/cplusplus-icon.png" alt="C++ Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/opengl-icon.png" alt="OpenGL Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/git-icon.png" alt="Git Icon" layout="constrained"/>                                
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/amazon-icon.png" alt="Amazon Icon" layout="constrained"/>
                            </div>
                            <div className={homeStyles.box_img}>
                                <StaticImage src="../images/tech-icon/google-analytics.png" alt="Google Analytics Icon" layout="constrained"/>                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}